import "./styles.css";

document.getElementById("app").innerHTML = `
<div class="content">
  <h1>Welcome!</h1>
  <input id="text" type="text" placeholder="Write some text here ..."/>
  <button class="btn">
    Copy to clipboard
  </button>
</div>
`;

const btn = document.querySelector(".btn");
const text = document.getElementById("text");

let aux = "";
let result = "";

text.addEventListener("change", () => {
  aux = text.value;
  aux = aux.replace(/[a,e,o,u]/g, "i");
  aux = aux.replace(/[á,é,ó,ú]/g, "í");
  aux = aux.replace(/[A,E,O,U]/g, "I");
  aux = aux.replace(/[Á,É,Ó,Ú]/g, "Í");
  result = aux;
});

btn.addEventListener("click", () => {
  text.value = result;
  text.select();
  document.execCommand("copy");
  text.setSelectionRange(0, 99999);
  text.value = "";
});
